<template>
  <div class="update-birthday">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      minDate: new Date(1920, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(this.value) // 
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidClick: true
      })
      try {
        // api-dayjs
        // const date = `${this.currentDate.getFullYear()}-${this.currentDate.getMonth() + 1}-${this.currentDate.getDate()}`
        const date = dayjs(this.currentDate).format('YYYY-MM-DD')
        await updateUserProfile({
          birthday: date
        })
        console.log(date)
        this.$toast.success('')
        // name
        this.$emit('input', date)
        // 
        this.$emit('close')
      } catch (err) {
        if (err && err.response && err.response.status === 409) { // 
          this.$toast.fail('')
          return
        }
        this.$toast.fail(',')
      }
    }
  }
}
</script>

