<template>
  <div class="update-photo">
    <img class="image" :src="image" ref="image">
    <van-nav-bar
      class="quit-confirm"
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    ></van-nav-bar>
  </div>
</template>

<script>
import { updateUserPhoto } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs' // jsvuereact
export default {
  name: 'UpdatePhoto',
  props: {
    file: {
      type: File, // file
      required: true
    }
  },
  data () {
    return {
      // ,urlurl
      // createObjectURLblobfilefileblob
      // URLblob
      image: window.URL.createObjectURL(this.file),
      cropper: null // 
    }
  },
  methods: {
    getCroppedCanvas () {
      return new Promise(resolve => { // blobpromiseblob
        this.cropper.getCroppedCanvas().toBlob((blob) => { // 
          resolve(blob)
        })
      })
    },
    async onConfirm () {
      // api
      this.$toast.loading({
        message: '',
        forbidClick: true,
        duration: 0 // 0
      })
      try {
        // headercontent-typemultipart/form-dataFormData{}
        const file = await this.getCroppedCanvas() // 
        const fd = new FormData() // FormData
        fd.append('photo', file)
        await updateUserPhoto(fd)
        this.$toast.success('')
        // 
        this.$emit('updatePhoto', window.URL.createObjectURL(file))
      } catch (err) {
        this.$toast.fail('')
      } finally {
        // 
        this.$emit('close')
      }
    }
  },
  mounted () {
    // dom
    const image = this.$refs.image
    this.cropper = new Cropper(image, {
      viewMode: 1,
      dragMode: 'move',
      aspectRatio: 1,
      // autoCropArea: 1,
      cropBoxMovable: false,
      cropBoxResizable: false,
      background: false,
      movable: true
    })
    console.log(this.cropper)
  }
}
</script>

<style scoped lang="less">
.quit-confirm {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
}
// 
/* Ensure the size of the image fit the container perfectly */
.image {
  display: block;

  /* This rule is very important, please don't ignore this */
  max-width: 100%;
}
</style>
