<template>
  <div class="update-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <!--  -->
    <div class="name-field-wrap">
      <van-field
        v-model="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      ></van-field>
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateName',
  props: {
    value: {
      type: String
    }
  },
  data () {
    return {
      localName: this.value // dataprop
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidClick: true
      })
      try {
        // api
        const { data } = await updateUserProfile({
          name: this.localName
        })
        console.log(data)
        this.$toast.success('')
        // name
        this.$emit('input', this.localName)
        // 
        this.$emit('close')
      } catch (err) {
        if (err && err.response && err.response.status === 409) { // 
          this.$toast.fail('')
          return
        }
        this.$toast.fail(',')
      }
    }
  }
}
</script>

<style>
  .van-popup {
    background-color: #f5f7f9;
  }
  .name-field-wrap {
    padding: 10px;
  }
</style>
