<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      class="app-nav-bar"
      title=""
      left-arrow
      @click-left="$router.back()"
    />
    <!--  -->
    <!-- ,* -->
    <!--  -->
    <input
      type="file"
      hidden
      ref="file"
      accept="image/*"
      @change="onFileChange"
    >
    <van-cell title="" center is-link @click="$refs.file.click()">
      <van-image
        width="30"
        height="30"
        round
        fit="cover"
        :src="user.photo"
      />
    </van-cell>
    <van-cell title="" is-link :value="user.name" @click="isEditNameShow=true"></van-cell>
    <van-cell title="" is-link :value="user.gender===0?'':''" @click="isEditGenderShow=true"></van-cell>
    <van-cell title="" is-link :value="user.birthday" @click="isEditBirthdayShow=true"></van-cell>
    <!--  -->
    <van-popup v-model="isEditNameShow" position="bottom" :style="{ height: '100%' }">
      <!-- v-modelv-model, -->
      <!-- ----------- -->
      <!-- .syncabc, -->
      <!-- 1<UpdateName :abc="user.gender" @update:abc="user.gender=$event"></UpdateName> -->
      <!-- 2<UpdateName :gender.sync="user.gender"></UpdateName> -->
      <!-- <button @click="$emit('update:abc',0)"></button> -->
      <UpdateName
        v-if="isEditNameShow"
        @close="isEditNameShow=false"
        v-model="user.name"
      ></UpdateName>
    </van-popup>
    <!--  -->
    <van-popup v-model="isEditGenderShow" position="bottom">
      <UpdateGender
        v-model="user.gender"
        @close="isEditGenderShow=false"
      ></UpdateGender>
    </van-popup>
    <!--  -->
    <van-popup v-model="isEditBirthdayShow" position="bottom">
      <UpdateBirthday
        v-if="isEditBirthdayShow"
        v-model="user.birthday"
        @close="isEditBirthdayShow=false"
      ></UpdateBirthday>
    </van-popup>
    <!--  -->
    <van-popup
      class="update-photo-popup"
      v-model="isEditPhotoShow"
      position="bottom"
      style="height:100%"
    >
      <UpdatePhoto
        v-if="isEditPhotoShow"
        :file="photoBlob"
        @close="isEditPhotoShow=false"
        @updatePhoto="user.photo=$event"
      ></UpdatePhoto>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user'
import UpdateName from './components/UpdateName.vue'
import UpdateGender from './components/UpdateGender.vue'
import UpdateBirthday from './components/UpdateBirthday.vue'
import UpdatePhoto from './components/UpdatePhoto.vue'
export default {
  name: 'UserProfile',
  components: { UpdateName, UpdateGender, UpdateBirthday, UpdatePhoto },
  data () {
    return {
      user: {}, // 
      isEditNameShow: false, // 
      isEditGenderShow: false, // 
      isEditBirthdayShow: false, // 
      isEditPhotoShow: false, // 
      photoBlob: '' // 
    }
  },
  methods: {
    async loadUserProfile () {
      const { data } = await getUserProfile()
      this.user = data.data
    },
    onFileChange () {
      // 
      this.isEditPhotoShow = true
      // filesrc
      const file = this.$refs.file.files[0]
      this.photoBlob = file
      // value
      this.$refs.file.value = ''
    }
  },
  created () {
    this.loadUserProfile()
  }
}
</script>

<style scoped lang="less">
.update-photo-popup {
  background-color: #000;
}
</style>
