<template>
  <div class="update-gender">
    <van-picker
      show-toolbar
      :default-index="defaultIndex"
      :columns="columns"
      @cancel="$emit('close')"
      @confirm="onConfirm"
      @change="onGenderChange"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''],
      defaultIndex: this.value // 
    }
  },
  methods: {
    onGenderChange (picker, value, index) {
      this.defaultIndex = index
    },
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidClick: true
      })
      try {
        // api
        await updateUserProfile({
          gender: this.defaultIndex
        })
        this.$toast.success('')
        // gender
        this.$emit('input', this.defaultIndex)
        // 
        this.$emit('close')
      } catch (err) {
        this.$toast.fail(',')
      }
    }
  }
}
</script>

